export default {
	data() {
		return {
			title: '内容单',
			array: [],
			page:1,
			pagesize:10,
			isLoadMore:false,  //是否加载中
		}
	},
	onLoad() {
		this.getDataLists()
	},
	onReachBottom(){
		 if(!this.isLoadMore){  //此处判断，上锁，防止重复请求
			this.isLoadMore=true
			this.page+=1
			this.getDataLists()
		  }
	 },
	 onPullDownRefresh(){
		this.array= []
	 	this.getDataLists()
	  },
	methods: {
		openUrl(){},
		/**
		 *  查询数据列表
		 * */
		getDataLists: function() {
			this.$req({
					method:'GET',
					url:'/bank/app/contents',
					data:{
						"page": this.page,
						"per_page": this.pagesize
					}
			}).then( res => {
				  if(res.data.message.data){
					this.array=this.array.concat(res.data.message.data)
						if(res.data.message.data.length<this.pagesize){  //判断接口返回数据量小于请求数据量，则表示此为最后一页
							  this.isLoadMore=true                                             
						}else{
							  this.isLoadMore=false
						}
				  }else{
						this.isLoadMore=true
				}
			}).catch( error => {
				uni.showToast({title:error,icon:'none'})
				this.isLoadMore=false
				if(this.page>1){
					this.page-=1
				}
			})
		}
		
	}
}
